export const ACTION_CLOSE = Symbol('ACTION_CLOSE_CONTEXT_MENU');
export const ACTION_OPEN = Symbol('ACTION_OPEN_CONTEXT_MENU');

export function closeContextMenu() {
  return {
    type: ACTION_CLOSE,
  };
}

export function openContextMenu() {
  return {
    type: ACTION_OPEN,
  };
}

export const actions = {
  openContextMenu,
  closeContextMenu,
}